import React from "react";
import { useFormik } from 'formik';
import * as Yup from 'yup';
import PropTypes from 'prop-types';

import PopupWithForm from "./PopupWithForm";

function AddPlacePopup({ onAddPlace, isOpen, onClose }) {


  const formik = useFormik({
    initialValues: {
      title: "",
      pic: "",
    },
    validationSchema: Yup.object({
      title: Yup.string()
        .min(2, "–ù–∞–∑–≤–∞–Ω–∏–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –∫–æ—Ä–æ—á–µ 2 —Å–∏–º–≤–æ–ª–æ–≤")
        .max(30, "–ù–∞–∑–≤–∞–Ω–∏–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –ø—Ä–µ–≤—ã—à–∞—Ç—å 30 —Å–∏–º–≤–æ–ª–æ–≤")
        .required("–í–≤–µ–¥–∏—Ç–µ, –ø–æ–∂–∞–ª—É–π—Å—Ç–∞, –Ω–∞–∑–≤–∞–Ω–∏–µ"),
      pic: Yup.string()
        .url("–í–≤–µ–¥–∏—Ç–µ, –ø–æ–∂–∞–ª—É–π—Å—Ç–∞, –∫–æ—Ä—Ä–µ–∫—Ç–Ω—É—é —Å—Å—ã–ª–∫—É")
        .required("–í–≤–µ–¥–∏—Ç–µ, –ø–æ–∂–∞–ª—É–π—Å—Ç–∞, —Å—Å—ã–ª–∫—É –Ω–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ"),
    }),
    onSubmit: (values) => {
      onAddPlace({
        title: values.title,
        pic: values.pic,
      });
    },
  });

  React.useEffect(() => {
    formik.resetForm();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isOpen]);

  return (
    <PopupWithForm
      name="add-card"
      title="–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ"
      buttonText="–°–æ–∑–¥–∞—Ç—å"
      onSubmit={formik.handleSubmit}
      isOpen={isOpen}
      onClose={onClose}
      disabledSubmit={!formik.isValid}
    >
      <fieldset className="popup__fieldset">
        <input
          id="title-pic-input"
          className="popup__input"
          type="text"
          {...formik.getFieldProps("title")}
          placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –º–µ—Å—Ç–∞"
        />
        <span className="popup__error">
          {formik.touched.title && formik.errors.title ? formik.errors.title : null}
        </span>


        <input
          id="url-pic-input"
          className="popup__input popup__input_url_pic"
          type="url"
          {...formik.getFieldProps("pic")}
          placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
        />
                <span className="popup__error">
          {formik.touched.pic && formik.errors.pic ? formik.errors.pic : null}
        </span>
      </fieldset>
    </PopupWithForm>
  );
}

AddPlacePopup.propTypes = {
  onAddPlace: PropTypes.func.isRequired,
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
}

export default AddPlacePopup;
