import React from "react";
import { useFormik } from 'formik';
import * as Yup from 'yup';
import PropTypes from 'prop-types';

import PopupWithForm from "./PopupWithForm";

import { CurrentUserContext } from "../contexts/CurrentUserContext";

function EditProfilePopup({ onUpdateUser, isOpen, onClose }) {
  const currentUser = React.useContext(CurrentUserContext);

  const formik = useFormik({
    initialValues: {
      fullName: currentUser.name,
      profession: currentUser.about,
    },
    validationSchema: Yup.object({
      fullName: Yup.string()
        .min(2, "–ò–º—è –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –∫–æ—Ä–æ—á–µ 2 —Å–∏–º–≤–æ–ª–æ–≤")
        .max(40, "–ò–º—è –Ω–µ –¥–æ–ª–∂–Ω–æ –ø—Ä–µ–≤—ã—à–∞—Ç—å 40 —Å–∏–º–≤–æ–ª–æ–≤")
        .required("–í–≤–µ–¥–∏—Ç–µ, –ø–æ–∂–∞–ª—É–π—Å—Ç–∞, –∏–º—è"),
      profession: Yup.string()
        .min(2, "–ü—Ä–æ—Ñ–µ—Å—Å–∏—è –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –∫–æ—Ä–æ—á–µ 2 —Å–∏–º–≤–æ–ª–æ–≤")
        .max(200, "–ü—Ä–æ—Ñ–µ—Å—Å–∏—è –Ω–µ –¥–æ–ª–∂–Ω–æ –ø—Ä–µ–≤—ã—à–∞—Ç—å 200 —Å–∏–º–≤–æ–ª–æ–≤")
        .required("–í–≤–µ–¥–∏—Ç–µ, –ø–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø—Ä–æ—Ñ–µ—Å—Å–∏—é"),
    }),
    onSubmit: (values) => {
      onUpdateUser({
        name: values.fullName,
        about: values.profession,
      });
    },
  });

  return (
    <PopupWithForm
      name="edit-profile"
      title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
      buttonText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      onSubmit={formik.handleSubmit}
      isOpen={isOpen}
      onClose={onClose}
      disabledSubmit={!formik.isValid}
    >
      <fieldset className="popup__fieldset">

        <input
          id="name-input"
          className="popup__input popup__input_text_full-name"
          type="text"
          {...formik.getFieldProps("fullName")}
          placeholder="–í–∞—à–µ –∏–º—è"
        />
        <span className="popup__error name-input-error">
          {formik.touched.fullName && formik.errors.fullName ? formik.errors.fullName : null}
        </span>

        <input
          id="about-you-input"
          className="popup__input popup__input_text_profession"
          type="text"
          {...formik.getFieldProps("profession")}
          placeholder="–í–∞—à–∞ –ø—Ä–æ—Ñ–µ—Å—Å–∏—è"
          required
          minLength={2}
          maxLength={200} />
        <span className="popup__error name-input-error">
          {formik.touched.profession && formik.errors.profession ? formik.errors.profession : null}
        </span>

      </fieldset>
    </PopupWithForm>
  );
}

EditProfilePopup.propTypes = {
  onUpdateUser: PropTypes.func.isRequired,
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
}

export default EditProfilePopup;
